@use "../abstracts";

/*--------------------------------------------------------------
# Normalize
--------------------------------------------------------------*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
font,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td {
  border: 0;
  outline: 0;
  padding: 0;
  vertical-align: baseline;
}

html {
  -webkit-font-smoothing: none;
  text-rendering: optimizeLegibility !important;
  -webkit-font-smoothing: antialiased !important;
  overflow-x: hidden;
}

body {
  margin: 0;
  font-family: "Be Vietnam Pro", BlinkMacSystemFont, sans-serif;
  font-size: 16px;
  line-height: 1.4;
  font-weight: 400;
  color: #262e36;
  word-break: break-word;
  overflow-x: hidden;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
main,
menu,
nav,
section,
summary {
  display: block;
}

a:active {
  outline: 0;
}

figure {
  margin: 0;
}

button,
input,
optgroup,
select,
textarea {
  color: inherit;
  font: inherit;
  margin: 0;
}

input {
  &[type="search"],
  &[type="text"],
  &[type="password"] {
    padding: 8px;
    flex-grow: 1;
    min-width: 3em;
    border: 1px solid #949494;
    font-size: inherit;
    font-family: inherit;
    line-height: inherit;
  }
}

input,
button {
  &[type="submit"] {
    background: #f7f7f7;
    border: 1px solid #ccc;
    padding: 8px 0.625em;
    color: #32373c;
    margin-left: 0.625em;
    word-break: normal;
    font-size: inherit;
    font-family: inherit;
    line-height: inherit;
    cursor: pointer;
  }
}

.btn-main {
  line-height: 22px !important;
}

.btn-main {
  font-size: 18px;
  min-width: 119px;
  text-align: center;
  color: #000;
  display: inline-block;
  font-family: "Urbanist", BlinkMacSystemFont, sans-serif;
  font-weight: 600;
  border: 2px solid rgba(0, 0, 0, 0);
  background: rgba(0, 0, 0, 0);
  cursor: pointer;
  letter-spacing: 0.32px;
  border-radius: 30px;
  word-break: break-word;
  text-decoration: none;
  position: relative;
  transition: all 0.25s ease-in-out;
  padding: 14px 30px;
  margin: 0;
}

.btn-main a {
  color: inherit;
  display: inline-block;
  text-decoration: none;
}

.btn-main a:hover {
  color: inherit;
}

.btn-main-border {
  padding: 2px;
  border-radius: 32px;
  display: inline-block;
  cursor: pointer;
}

.btn-main-border .btn-main {
  border: 0;
}

.btn-main-border.btn-link-right-arrow {
  padding: 0;
}

.btn-main-border.btn-link-right-arrow .btn-main {
  padding: 0;
  min-width: auto;
}

.btn-main-border.btn-link-right-arrow .btn-main::after {
  content: "";
  display: inline-block;
  margin-left: 10px;
  position: relative;
  right: 0;
  top: -2px;
  width: 16px;
  height: 4px;
  -webkit-mask: url(../images/ic-arrow.svg) no-repeat 50% 50%;
  mask: url(../images/ic-arrow.svg) no-repeat 50% 50%;
  background-color: currentColor;
  -webkit-mask-size: cover;
  mask-size: cover;
  transition: all 0.25s ease-in-out;
}

.btn-main-border.btn-link-right-arrow .btn-main:hover::after {
  right: -2px;
}

.btn-main-border.btn-has-left-arrow .btn-main::before {
  content: "";
  display: inline-block;
  margin-right: 10px;
  position: relative;
  top: -3px;
  left: 0;
  width: 16px;
  height: 4px;
  -webkit-mask: url(../images/ic-arrow.svg) no-repeat 50% 50%;
  mask: url(../images/ic-arrow.svg) no-repeat 50% 50%;
  transform: rotate(180deg);
  background-color: #000;
  -webkit-mask-size: cover;
  mask-size: cover;
  transition: all 0.25s ease-in-out;
}

.btn-main-border.btn-has-left-arrow .btn-main:hover::before {
  left: -2px;
}

.btn-main-border.btn-has-right-arrow .btn-main {
  padding: 14px 56px 14px 30px;
}

.btn-main-border.btn-has-right-arrow .btn-main::after {
  content: "";
  display: inline-block;
  position: absolute;
  top: 50%;
  right: 30px;
  margin-top: 1px;
  transform: translateY(-50%);
  width: 16px;
  height: 4px;
  -webkit-mask: url(../images/ic-arrow.svg) no-repeat 50% 50%;
  mask: url(../images/ic-arrow.svg) no-repeat 50% 50%;
  background-color: #000;
  -webkit-mask-size: cover;
  mask-size: cover;
  transition: all 0.25s ease-in-out;
}

.btn-main-border.btn-has-right-arrow .btn-main:hover::after {
  right: 26px;
}

.btn-main-border.btn-primary-white {
  background-color: #fff;
  border: 2px solid #fff;
}

.btn-main-border.btn-primary-white .btn-main {
  color: #000;
}

.btn-main-border.btn-primary-white .btn-main::after {
  background-color: #000;
}

.btn-main-border.btn-primary-white:hover {
  background-color: rgba(0, 0, 0, 0) !important;
}

.btn-main-border.btn-primary-white:hover .btn-main {
  color: #fff;
}

.btn-main-border.btn-primary-white:hover .btn-main::after {
  background-color: #fff;
}

.btn-main-border.btn-secondary-white {
  background-color: #fff;
}

.btn-main-border.btn-secondary-white .btn-main {
  color: #fff;
}

.btn-main-border.btn-secondary-white .btn-main::after {
  background-color: #fff;
}

.btn-main-border.btn-secondary-white:hover .btn-main {
  background-color: #fff !important;
}

.btn-main-border.btn-secondary-white:hover .btn-main {
  color: #000;
}

.btn-main-border.btn-secondary-white:hover .btn-main::after {
  background-color: #000;
}

.btn-main-border.btn-primary-gradient-blue-1 {
