    }
    ul li:not(:last-child) {
      border-right: 1px solid rgba(255, 255, 255, 0.2);
    }
    .press-release-date p {
      margin: 0;
      border-radius: 6px;
      background: rgba(255, 255, 255, 0.2);
      padding: 6px 10px;
      display: inline-block;
      color: #fff;
      font-size: 18px;
      max-width: -moz-max-content;
      max-width: max-content;
    }
    .wp-block-read-more {
      &::after {
        content: "";
        display: inline-block;
        margin-left: 10px;
        position: relative;
        right: 0;
        top: -2px;
        width: 16px;
        height: 4px;
        -webkit-mask: url(../images/ic-arrow.svg) no-repeat 50% 50%;
        mask: url(../images/ic-arrow.svg) no-repeat 50% 50%;
        background-color: currentColor;
        -webkit-mask-size: cover;
        mask-size: cover;
        transition: all 0.25s ease-in-out;
      }
    }
  }
}

