/* Slick Slider Arrows */
ul.slick-dots {
  flex-wrap: wrap;
}
.md_hero_banner_slider,
.md_hero_banner_slider_v3 {
  .slick-arrow {
    width: 40px !important;
    height: 40px !important;
    font-size: 0 !important;
    background: rgba(0, 0, 0, 0) !important;
    cursor: pointer !important;
    top: 40%;
    transform: translateY(-50%);
    border: none !important;
    position: absolute;
    display: block;
  }

  .slick-arrow:before {
    width: 16px;
    height: 16px;
    content: "";
    display: block;
    position: absolute;
    top: 11px;
    font-size: 0 !important;
  }

  .slick-prev {
    left: -40px;
    z-index: 1;
  }

  .slick-prev:before {
    border-top: 3px solid #000000;
    border-left: 3px solid #000000;
    transform: rotate(-45deg);
  }

  .slick-prev[aria-disabled="true"] {
    opacity: 0.4;
    pointer-events: none;
  }

  .slick-next {
    right: -50px;
  }

  .slick-next:before {
    border-top: 3px solid #000000;
    border-left: 3px solid #000000;
    transform: rotate(135deg);
  }

  .slick-next[aria-disabled="true"] {
    opacity: 0.4;
    pointer-events: none;
  }
}
/* Slick Slider Dots */
.slick-dots {
  text-align: center; /* Center align dots */
  margin-top: 20px; /* Space above dots */
}

.slick-dots li {
  display: inline-block;
  margin: 0 5px; /* Space between dots */
}
